

#main {
    padding: 40px;
    display: flex;
}

/*
 *  Side menu
 */

#sidemenu {
    border: solid gray 1px;
    border-radius: 5px;
    margin-bottom: auto;
    /* width: 300px; */
    min-width: 200px;
    max-width: 200px;
    text-align: center;
    background-color: white;
}

#sidemenu ul {
    padding: 0px;
}

#sidemenu li {
    margin: 15px;
    list-style-type: none;
    cursor: pointer;
}

#sidemenu li:hover {
    color: green;
}

/*
 *  Content
 */

#content {
    margin-left: 40px;
}

#distinct-io {
    margin-left: 40px;
}

#input-separater {
    resize: none;
}

#output-separater {
    resize: none;
}

#input {
    width: 40%;
}

#output {
    width: 40%;
}

#duplicate-statistics {
    margin-top: 20px;
    margin-left: 40px;
    width: 80%;
    border-collapse: collapse ;
    text-align: center;
}

#duplicate-statistics th{
    border: 1px gray solid;
    border-spacing: 0ch;
    font-weight: normal;
}

#duplicate-statistics td{
    border: 1px gray solid;
    border-spacing: 0ch;
}

/*
 *  Tex2Img
 */

 #Tex2Img {
     margin-left: 40px;
 }

 #Tex2Img-io {
     margin-left: 40px;
 }

 #Tex2Img-input {
     resize: none;
 }

 /*
 *  Dots
 */

 #dots {
     margin-left: 40px;
 }

 #dots-io {
     display: flex;
 }

 #dots-canvas {
     margin: 10px;
     padding: 1px;
     border: gray 3px solid;
 }

 #tools {
     margin: 10px;
     border-radius: 10px;
     border: gray solid 3px;
     width: 500px;
     height: 500px;
     position: relative;
     background-color: gray;
 }

 #layer-tab {
     text-align: center;
     position: absolute;
     width: 100px;
     left: 5%;
     top: -30px;
     border: 3px solid gray;
     border-top-left-radius: 10px;
     border-top-right-radius: 10px;
     font-weight: bold;
 }

 #layer-tab:hover {
     color: green;
     cursor: pointer;
 }

 #pen-tab {
    width: 100px;
    text-align: center;
    position: absolute;
    left: 30%;
    top: -30px;
    border: 3px solid gray;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    font-weight: bold;
}

#pen-tab:hover {
    color: green;
    cursor: pointer;
}

#download-tab {
    width: 100px;
    text-align: center;
    position: absolute;
    left: 55%;
    top: -30px;
    border: 3px solid gray;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    font-weight: bold;
}

#download-tab:hover {
    color: green;
    cursor: pointer;
}

.dots-active-tab {
    background-color: gray;
    color: white;
}

.dots-inactive-tab {
    background-color: white;
    color: gray;
}

.dots-panel {
    color:  white;
}

.dots-panel ul {
    padding: 0;
}

.dots-panel li {
    margin-top: 20px;
}

.dots-active-layer-list {
    text-align: center;
    color: white;
    width: 70%;
    margin-left: 30px;
    border-radius: 3px;
    background-color:darkseagreen;
    list-style-type: none;
    font-weight: 500;
}

.dots-active-layer-list:hover {
    color: green;
    cursor: pointer;
}

.dots-inactive-layer-list {
    text-align: center;
    color: gray;
    width: 70%;
    margin-left: 30px;
    border-radius: 3px;
    background-color:white;
    list-style-type: none;
    font-weight: 500;
}

.dots-inactive-layer-list:hover {
    color: green;
    cursor: pointer;
}

/* 
 * Dounuts
 */

 #dounuts-canvas-wrapper {
     z-index: -1;
     top: 0;
     left: 0;
     width: 100%;
     height: 100%;
     position: absolute;
     background-color: #031024;
 }